import React, { useState } from "react";
import {
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Col,
  Container,
  Row,
} from "reactstrap";
import Table from "../../shared/components/Table/DataTable";
import Films from "./components/Films";
import Starships from "./components/Starships";
import People from "./components/People";
import Vehicle from "./components/Vehicle";
import Species from "./components/Species";

const Dashboard = () => {
  const [dropdownOpen, setDropdownOpen] = useState(false);
  const toggle = () => setDropdownOpen((prevState) => !prevState);

  return (
    <Container className="dashboard_container" md={12}>
      <Col>
        <div className="dropdown-zone">
          <div>
          <Dropdown isOpen={dropdownOpen} toggle={toggle} className="dropdown-zone-btn">
              <DropdownToggle caret>Current Year</DropdownToggle>
              <DropdownMenu>
                <DropdownItem>2020</DropdownItem>
                <DropdownItem>2019</DropdownItem>
                <DropdownItem>2018</DropdownItem>
              </DropdownMenu>
            </Dropdown>
          </div>
          <div>
          <Dropdown isOpen={dropdownOpen} toggle={toggle} className="dropdown-zone-btn">
              <DropdownToggle caret>Current Week</DropdownToggle>
              <DropdownMenu>
                <DropdownItem>Week 1</DropdownItem>
                <DropdownItem>Week 2</DropdownItem>
                <DropdownItem>Week 3</DropdownItem>
              </DropdownMenu>
            </Dropdown>
          </div>
        </div>
      </Col>

      <Row>
        <Row className="bashboard_datas">
          <Films />
          <Starships />
          <People />
          <Vehicle />
          <Species />
        </Row>
      </Row>
      <Row>
        <Table />
      </Row>
    </Container>
  );
};

export default Dashboard;
