import React, { useState } from "react";
import { Col, Row, Container, Button } from "reactstrap";
import {  useHistory } from "react-router-dom";
import { Formik, Form, ErrorMessage } from "formik";
import { passwordPattern } from "../../shared/helpers/validationHelpers";
import astroIcon from "../../shared/assets/icon/star.svg";
import rocketIcon from "../../shared/assets/icon/astro.svg";
import * as Yup from "yup";
import Input from "../../shared/components/Form/Input";


const Home = () => {
  const history = useHistory();


  const initialValues = {
    email: "",
    password: "",
  };

  const validationSchema = Yup.object({
    email: Yup.string().email().required(),
    password: Yup.string()
      .matches(
        passwordPattern,
        "Your password must include uppercase, numbers, letters and a minimum  of 8 characters"
      )
      .required(),
  });

  const onSubmit = async (formValues) => {
    history.push("/dashboard");
  };

  return (
    <div className="landing__menu">
      <Container>
        <Row>
          <Col md={4} className="left__col">
            <div className="left__col--inner">
              <div className="left__col--text">
                Welcome to Star Wars the Clone Wars
              </div>
              <div className="left__col--icon">
                <img src={rocketIcon} className="icon__rockets" />
              </div>
            </div>
          </Col>
          <Col md={6}>
            <div className="account account--photo">
              <div className="account__wrapper">
                <div className="account__card">
                  <div className="account__head">
                    <h4 className="account__subhead subhead">
                      Sign in to continue to <br />
                      your account
                    </h4>
                  </div>
                  <Formik
                    initialValues={initialValues}
                    validationSchema={validationSchema}
                    onSubmit={onSubmit}
                  >
                    {(formik) => {
                      return (
                        <Form className="form login__box">
                          <div className="input-container-custom mb-3">
                            <label className="form__label--sm">
                              Email address
                            </label>
                            <Input
                              className="input__text--field"
                              name="email"
                              type="email"
                              placeholder="Email address"
                            />
                            <p className="error_para">
                              <ErrorMessage name="email" />
                            </p>
                          </div>
                          <div className="input-container-custom mb-2">
                            <label className="form__label--sm">Password</label>
                            <Input
                              className="input__text--field"
                              name="password"
                              id="input-custom-password"
                              type="text"
                              placeholder="Enter strong password"
                            />
                            <p className="error_para">
                              <ErrorMessage name="password" />
                            </p>
                          </div>
                          <div className="account__btns register__btns mt-3">
                            <Button
                              type="submit"
                              color="primary"
                              className="account__btn account__btn--primary btn"
                              disabled={!formik.isValid}
                            >
                              Login
                            </Button>
                          </div>
                        </Form>
                      );
                    }}
                  </Formik>
                </div>
              </div>
            </div>
          </Col>
          <Col md={2}>
            <div className="right__col--inner">
              <div className="right__col--icon">
                <img src={astroIcon} className="icon__astro" />
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default Home;
